.main {
    display: grid;
    grid-template-columns: 70% 30%;
    width: 78.714vw;
    margin-top: 50px;
    margin-bottom: 50px;
    background-image: linear-gradient(45deg, #5217af, #9d5eff);
    border-radius: 16px;
    -webkit-border-radius: 16px;
    -moz-border-radius: 16px;
    -ms-border-radius: 16px;
    -o-border-radius: 16px;
}

.contextMain {
    grid-column: 1/2;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
    padding: 2vw 0vw 2vw 2vw;
}

.contextMain h1{
    color: #ffe4d2;
    font-family: 'BR Hendrix', Arial, sans-serif;
    font-size: 72px;
    font-weight: 400;
    line-height: 100%;
    text-align: left;
    padding: 10px;
}

.contextMain p {
    color: #fff;
    font-size: 24px;
    font-weight: 400;
    line-height: 150%;
    text-align: left;
    padding: 10px;
}

.marketPics{
    padding: 10px;
}

.marketPics img{
    padding: 5px;
}

.mobilePic {
    grid-column: 2/3;
    width: 350px;
    margin-top: 20px;
}

@media (max-width:769px) {
    .mobilePic{
        display: none;
    }
    .main{
        display: flex;
    }
  }
  