.coinLanding{
  width: 78.714vw;
  margin: 50px auto;
  padding: 20px 10px;
  box-sizing: border-box;
  border-radius: 16px;
  -webkit-border-radius: 16px;
  -moz-border-radius: 16px;
  -ms-border-radius: 16px;
  -o-border-radius: 16px;
  background-image: linear-gradient(45deg, #5217af, #9d5eff);
}

.input {
  width: 80%;
  height: 20px;
  margin: 20px;
  padding: 15px 25px;
  box-sizing: border-box;
  border: 1.3px dotted #fff;
  border-radius: 5px;
  background-image: linear-gradient(45deg, #5217af, #9d5eff);
  transition: all 0.2s ease;
}

.input:focus {
  outline: none;
  background: #fff;
  border: none;
}

.coinContainer h3 {
  color: #fff;
}