.container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin:10px;
  padding: 15px 15px;
  font-size: 12px;
  color: #212529;
  background-color: #fff;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.container:last-child {
  border: none;
}

.image {
  width: 30px;
}

.name {
  width: 140px;
  font-size: 14xp;
  margin-left: 15px;
  text-align: left;
  font-weight: bold;
  color: #333333;
}

.symbol {
  text-align: left;
  width: 100px;
}

.currentPrice {
  text-align: left;
  width: 100px;
}

.greenPriceChange,
.redPriceChange {
  width: 100px;
  text-align: left;
  font-weight: bold;
}

.redPriceChange {
  color: rgb(255, 38, 38);
}

.greenPriceChange {
  color: rgb(0, 204, 0);
}

@media (max-width:885px) {
  .image , .marketCap{
    display: none;
  }
}